.container {
  height: 280px;
  width: 210px;
  margin: 0;
  transition: .2s ease-out;
  }
  
  .image {
    width: 100%;
    height: 100%;
    transition: .2s ease;
  }
  
  .middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
  }
  
  .container:hover{
    transform: scale(1.05);
  }
  
  .container:hover .middle {
    opacity: 1;
  }
  
  .text {
    background-color: #4CAF50;
    color: white;
    font-size: 16px;
    padding: 16px 32px;
  }

